@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  background-color: #f5f5f5;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-root{background-color:#f5f5f5}.app-root .content-root{margin-top:4em}.headbar{background-color:#000000 !important;height:4em !important}.headbar .toolbar{height:4em !important;display:flex;justify-content:center}.headbar .toolbar .logoHeadbar{font-family:'Roboto', sans-serif;font-weight:300;font-size:x-large;margin-left:1em;margin-right:1em}

.input-component{padding-top:3em;display:flex;justify-content:center}.input-component .text-field{display:flex;height:3.5em}.input-component .text-field .prefix-area{height:3.5em;display:flex;flex-direction:column;justify-content:center}.input-component .text-field .prefix-area .prefix-text{font-family:'Roboto', sans-serif;font-weight:600;color:darkGrey;font-size:x-large;padding-left:0.5em;padding-right:0.5em}.input-component .text-field .input-base{max-width:30em;width:20em}.input-component .text-field .search-button-area{height:3.5em;display:flex;flex-direction:column;justify-content:center}

.app-root{background-color:#f5f5f5}.app-root .content-root{margin-top:4em}

